.logo {
  width: 55px;
  height: 60px;
  grid-column: span 2;
  justify-self: center;
  display: block;
  text-decoration: none;

  @include on-tablet {
    grid-column: 2/3;
  }

  @include on-desktop {
    grid-column: span 2;
  }

  &__name {
    color: $color-title;
    text-align: center;
    font-family: Montserrat, sans-serif;
    font-size: 30px;
    font-weight: 700;
    line-height: 100%;

    @include on-desktop {
      font-size: 36px;
    }

    @include hover(color, $color-hover);
  }
}
